.footer {
    &__container {
        background: #33383C;
    }

    &__inner-wrapper {
        max-width: 1402px;
        margin-left: auto;
        margin-right: auto;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding-top: 52px;
        padding-bottom: 27px;
    }

    &__navigation-list {
        display: flex;
        max-width: 1140px;
        justify-content: space-between;
        flex-wrap: wrap;
    }

    &__item {
        display: inline-block;
        height: auto;
        width: 246px;
        margin-bottom: 25px;
    }

    &__item-title {
        cursor: pointer;
        margin-bottom: 12px;
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 700;
        font-size: 14px;
        line-height: 17px;
        color: #FFFFFF;
    }


    &__item-list {
        display: flex;
        flex-direction: column;
    }

    &__list-item {
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 29px;
        color: #878F99;
        cursor: pointer;

        &:hover {
            color: #FFFFFF;
        }
    }

    &__more-container {}

    &__more-btn {
        cursor: pointer;
        border: none;
        background: none;
        gap: 8.88px;
        display: flex;
        align-items: center;
        font-family: 'Gilroy';
        font-size: 15px;
        line-height: 18px;
        color: #66717A;
        margin-bottom: 34px;
    }

    &__hr {
        display: inline-block;
        height: 1px;
        width: 100%;
        background-color: #4C565E;
        margin-bottom: 27px;
    }

    &__nav-wrapper {
        display: flex;
        align-items: center;
        max-width: 1140px;
        justify-content: space-between;
        width: 100%;
    }

    &__nav-link {
        font-family: 'Gilroy';
        font-size: 15px;
        line-height: 18px;
        cursor: pointer;
        color: #66717A;

        &:hover {
            color: #05F3B3;
        }
    }
}


