.footer__top-wrapper {
    background: #282B30;
    padding-top: 23px;
    padding-bottom: 22px;
}

.footer__top {
    max-width: 1140px;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.footer__logo-wrapper {
    display: flex;
    align-items: center;
    gap: 14px;
    text-decoration: none;
}

.footer__logo-icon {}

.footer__logo-text {
    font-family: 'Gilroy';
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 17px;
    color: #FFFFFF;
    width: 149px;
}

.footer__top-text {
    font-family: 'Gilroy';
    font-style: normal;
    font-weight: 500;
    font-size: 15px;
    line-height: 18px;
    color: #98A4B7;
    width: 307px;
}

.footer__media-wrapper {
    display: flex;
    align-items: center;
    gap: 5px;
}

.footer__media-icon {
    cursor: pointer;
}