.profile__link {
    color: #414D6A;
}

.activecha {
    font-weight: 900;
    color: #4E72CA;

    &::before {
        content: '';
        display: inline-block;
        position: absolute;
        width: 100px;
        height: 2px;
        bottom: -10px;
        left: 5%;
        background-color: #4E72CA;
    }
}

.active {
    font-weight: 900;
    color: #4E72CA;

    &::before {
        content: '';
        display: inline-block;
        position: absolute;
        width: 100px;
        height: 2px;
        left: 5%;
        background-color: #4E72CA;
    }

}


.balance {

    &__wrapper {
        display: flex;
        flex-direction: column;
    }

    &__inner-wrapper {
        max-width: 1140px;
        width: 100%;
        margin-left: auto;
        margin-right: auto;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__name {
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 700;
        font-size: 17px;
        line-height: 21px;
        text-align: center;
        color: #414D6A;
    }

    &__right-wrapper {
        display: flex;
        align-items: center;
    }

    &__nums {
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 500;
        font-size: 17px;
        line-height: 21px;
        text-align: center;
        color: #304FFF;
        margin-right: 26px;
    }

    &__right-btn {
        display: flex;
        align-items: center;
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 19px;
        color: #000000;
        padding-right: 22px;
        background: #FFFFFF;
        border: 1px solid #41D481;
        border-radius: 100px 5px 5px 100px;
    }

    &__right-plus {
        display: inline-block;
        font-weight: 700;
        padding: 10px 14px 10px 14px;
        background: #41D481;
        border-radius: 50%;
        margin-left: -6px;
        margin-right: 14px;
    }

    &__line {
        display: inline-block;
        height: 1px;
        width: 100%;
        background-color: rgba(151, 151, 151, 0.34);
        margin-top: 8px;
        margin-bottom: 16px;
    }

    &__list {
        display: flex;
        align-items: center;
        max-width: 1140px;
        margin-left: auto;
        margin-right: auto;
        width: 100%;
        gap: 80px;
    }

    &__item {
        position: relative;
    }

    &__link {
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 500;
        font-size: 17px;
        line-height: 21px;
        text-align: center;
        text-decoration: none;

        &::before {
            bottom: -18px;
        }
    }
}

.profile {

    &__container {
        background: rgba(0, 0, 0, 0.0470165);
        padding-bottom: 76px;
    }

    &__inner-wrapper {
        max-width: 1140px;
        margin-left: auto;
        margin-right: auto;
    }

    &__headings-wrapper {
        display: flex;
        align-items: center;
        padding-top: 15px;
        margin-bottom: 11px;
    }

    &__heading1 {
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 17px;
        color: #000000;
        margin-right: 28px;
    }

    &__heading2 {
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 17px;
        color: #000000;
        margin-right: 232px;
    }

    &__heading3 {
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 17px;
        color: #000000;
        margin-right: 123px;
    }

    &__heading4 {
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 17px;
        color: #000000;
        margin-right: 103px;
    }

    &__heading5 {
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 17px;
        color: #000000;
    }

    &__posts-list {
        display: flex;
        flex-direction: column;
        gap: 10px;
    }

    &__item {
        display: flex;
        flex-direction: column;
        background-color: #FFF;
    }

    &__item-top {
        padding-top: 19px;
        padding-left: 17px;
        padding-right: 16px;
        display: flex;
        align-items: center;
    }

    &__top-title-wrapper {
        display: flex;
        align-items: center;
        gap: 16px;
        margin-right: 96px;
    }

    &__top-img {
        width: 49px;
        height: 49px;
        object-fit: cover;
    }

    &__top-title {
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 500;
        font-size: 17px;
        line-height: 21px;
        color: #414D6A;
    }

    &__top-salary-wrapper {
        display: flex;
        flex-direction: column;
        margin-right: 65px;
    }

    &__top-sal {
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 500;
        font-size: 15px;
        line-height: 18px;
        color: #414D6A;
    }

    &__top-date-wrapper {
        display: flex;
        flex-direction: column;
        margin-right: 50px;
    }

    &__top-dat {
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 17px;
        color: #414D6A;
    }

    &__btn-wrapper {
        display: flex;
        align-items: center;
        gap: 14px;
        margin-right: 21px;
        background: #FFFFFF;
        border: 1px solid #41D481;
        border-radius: 100px 5px 5px 100px;
        padding-right: 20px;
        cursor: pointer;

        &-blue {
            border: 1px solid #304FFF;
        }
    }

    &__green {
        width: 34px;
        height: 35.75px;
        object-fit: cover;
    }

    &__blue {
        width: 34px;
        height: 35.75px;
        object-fit: cover;
    }

    &__more {
        border: none;
        background: none;
        width: 35px;
        height: 35px;
        margin-left: 2px;
        background: #FFFFFF;
        box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.196643);
        border-radius: 50%;
        cursor: pointer;
    }

    &__more-img {
        width: 16.75px;
        height: 4.48px;
        object-fit: cover;
    }

    &__item-line {
        display: inline-block;
        width: 100%;
        height: 1px;
        background-color: rgba(151, 151, 151, 0.246886);
        margin-top: 10px;
        margin-bottom: 8px;
    }

    &__item-bottom {
        display: flex;
        align-items: center;
        width: 422px;
        justify-content: space-between;
        padding-left: 22px;
        padding-bottom: 14px;
    }

    &__item-bottom-text {
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 17px;
        color: #414D6A;

        &:first-child {
            color: #304FFF;
        }
    }
}

.container {}

.boxdel {
    box-shadow: none;
}

.width-reg {
    width: 1140px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 17px;
    margin-bottom: 0;
}

.nestedLink {
    margin-left: auto;
    margin-right: auto;
