.sort {

    &__container {}

    &__input-wrapper {
        padding: 15px 0 11.28px 17px;
        max-width: 1140px;
        width: 100%;
        margin-top: 31px;
        margin-bottom: 13px;
        display: flex;
        align-items: center;
        background: #FFFFFF;
        border: 1px solid rgba(151, 151, 151, 0.496575);
        border-radius: 6px;
    }

    &__input-img {
        width: 23.8px;
        height: 24.72px;
        object-fit: contain;
        margin-right: 17.2px;
    }

    &__input-text {
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 19px;
        color: #787993;
    }

    &__input-link {
        color: #4E72CA;
        text-decoration: underline;
    }

    &__type {}

    &__type-list {
        display: flex;
        align-items: center;
        gap: 13px;
        margin-bottom: 25px;
    }

    &__type-item {
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 700;
        font-size: 13px;
        line-height: 16px;
        padding: 2px 10px 3px 11px;
        border-radius: 3px;

        &:nth-child(1) {
            background: #E5E3FF;
            color: #704FFF;
        }

        &:nth-child(2) {
            background: #E4F6FF;
            color: #57B4E2;
        }

        &:nth-child(3) {
            background: #D2FFDB;
            color: #47B95E;
        }

        &:nth-child(4) {
            background: #F6DAC2;
            color: #874C1A;
        }

        &:nth-child(5) {
            display: flex;
            align-items: center;
            gap: 14px;
            color: #787993;
            background: #D8D8D8;
        }

        &:nth-child(6) {
            display: flex;
            align-items: center;
            gap: 14px;
            color: #787993;
            background: #D8D8D8;
        }

        &:nth-child(7) {
            display: flex;
            align-items: center;
            gap: 14px;
            color: #787993;
            background: #D8D8D8;
        }
    }

    &__rubric {
        display: flex;
        flex-direction: column;
        gap: 23px;
    }

    &__rubric-now-wrapper {
        display: flex;
        align-items: center;
    }

    &__rubric-head {
        font-family: 'Gilroy';
        font-size: 16px;
        line-height: 19px;
        letter-spacing: -6.7332e-05px;
        color: #787993;
        margin-right: 54px;
    }

    &__rubric-wrapper {
        display: flex;
        align-items: center;
        padding: 7px 23.5px 6px 8px;
        background: #FFFFFF;
        border: 1px solid rgba(151, 151, 151, 0.496575);
        border-radius: 27px;
        width: 472px;
    }

    &__rubric-item {
        margin-right: 7px;
        background: #EBF5FF;
        border: 1px solid #B3D9FF;
        border-radius: 20.5px;
        padding-left: 20px;
        display: flex;
        align-items: center;
        gap: 6px;
    }

    &__rubric-name {
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 500;
        font-size: 15px;
        line-height: 18px;
        color: #2979FF;
    }

    &__rubric-cross {
        background: #EBF5FF;
        border: 1px solid #B3D9FF;
        border-radius: 20.5px;
        padding: 14px 15.48px 14.48px 13px;
    }

    &__rubric-arrow {
        padding-left: 85.5px;
    }

    &__rubric-slider-wrapper {
        display: flex;
        align-items: center;
        gap: 41px;
    }

    &__rubric-slider-name {
        font-family: 'Gilroy';
        font-size: 16px;
        line-height: 19px;
        letter-spacing: -6.7332e-05px;
        color: #787993;
    }

    &__rubric-slider {
        max-width: 923px;
        width: 100%;
    }

    &__rubric-sorting {
        display: flex;
        align-items: center;
        margin-bottom: 29px;
    }

    &__rubric-condition {
        display: flex;
        align-items: center;
        gap: 42.7px;
        margin-right: 70.59px;

        & .sort__rubric-arrow {
            padding-left: 0;
        }
    }

    &__rubric-condition-title {
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 700;
        font-size: 14px;
        line-height: 17px;
        color: #414D6A;
    }

    &__rubric-type {
        display: flex;
        align-items: center;
        gap: 42.7px;
        margin-right: 68.59px;

        & .sort__rubric-arrow {
            padding-left: 0;
        }
    }

    &__rubric-type-title {
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 700;
        font-size: 14px;
        line-height: 17px;
        color: #414D6A;
    }

    &__rubric-location {
        display: flex;
        align-items: center;
        gap: 63.7px;

        & .sort__rubric-arrow {
            padding-left: 0;
        }

        margin-right: 240px;
    }

    &__rubric-location-title {
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 700;
        font-size: 14px;
        line-height: 17px;
        padding-left: 19px;
        color: #414D6A;
    }

    &__rubric-btn {
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 700;
        font-size: 15px;
        line-height: 19px;
        text-align: center;
        color: #FFFFFF;
        padding: 13px 11px 14px 12px;
        background: #304FFF;
        box-shadow: 3px 6px 14px #9CC1FF;
        border-radius: 22px;
        border: none;
        cursor: pointer;
        min-width: 135px;
    }

    &__line {
        display: inline-block;
        width: 100%;
        height: 1px;
        background-color: rgba(151, 151, 151, 0.393739);
        margin-bottom: 28.9px;
    }

    &__products-list {}

    &__now-searching {
        display: flex;
        align-items: center;
        margin-bottom: 25px;
    }

    &__now-searching-img {
        width: 19px;
        height: 20px;
        object-fit: contain;
        margin-right: 7px;
    }

    &__now-searching-heading-top {
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 700;
        font-size: 20px;
        line-height: 25px;
        color: #414D6A;
    }

    &__now-searching-list {
        display: flex;
        align-items: center;
        overflow-x: hidden;
        padding-left: 38px;
        border-left: 1px solid rgba(151, 151, 151, 0.356686);
        border-right: 1px solid rgba(151, 151, 151, 0.356686);
        height: 96px;
        gap: 64px;
        margin-bottom: 35.4px;
    }

    &__now-searching-item {
        display: flex;
        flex-direction: column;
        flex-wrap: nowrap;
        flex-shrink: 0;
        width: 198px;
    }

    &__now-searching-heading {
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 700;
        font-size: 16px;
        line-height: 24px;
        color: #4E72CA;
    }

    &__now-searching-text {
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 24px;
        color: #AAAAB6;
    }

    &__now-searching-item-img {
        width: 12.8px;
        height: 13.29px;
        object-fit: contain;
        margin-right: 5px;
    }

    &__interesting {}

    &__interesting-heading {
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 700;
        font-size: 20px;
        line-height: 25px;
        color: #41D481;
        margin-bottom: 18px;
    }

    &__interesting-list {
        display: flex;
        height: 100%;
        justify-content: space-between;
        position: relative;
        &::after{
            content: '';
            display: inline-block;
            width: 44px;
            height: 44px;
            background-image: url('./../../assets/images/arrow-blue.svg');
            background-position: center;
            background-size: contain;
            background-repeat: no-repeat;
            position: absolute;
            right: -23px;
            top: 174px;
            cursor: pointer;
        }
    }

    &__interesting-item {
        padding: 30px 26px 36px 26px;
        background: #FFFFFF;
        height: 331px;
        border: 1px solid rgba(120, 121, 147, 0.243461);
        margin-bottom: 40px;
    }
}

.topped__item {
    position: relative;
    background: rgba(65, 212, 129, 0.225289);
    border-radius: 6px;
    margin-bottom: 21px;
    .product__item-title {
        padding-left: 15px;
    }
    .product__item-time {
        padding-left: 15px;
    }
    .product__item-price {
        padding-left: 15px;
    }
}

.topped {
    position: absolute;
    top: 0;
    left: 0;
}