.container {
    max-width: 1140px;
    margin-right: auto;
    margin-left: auto;  
}

*,
*::before,
*::after {
    box-sizing: border-box;
    // Mirmuhsin
}

html {
    height: 100%;
    scroll-behavior: smooth;
    overflow-x: hidden;
}

main {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    overflow-x: hidden;
}

footer {
    overflow-x: hidden;
}

body {
    margin: 0;
    padding: 0;
}

img {
    vertical-align: middle;
}

ul, ol, li {
    margin: 0;
    padding: 0;
    list-style-type: none;
}

h1, h2, h3, h4, h5, h6, p {
    margin: 0;
}
