.slider {
    user-select: none;
    background: rgba(182, 202, 251, 0.159986);
    box-shadow: inset 0px 21px 13px rgba(182, 202, 251, 0.225402);
    padding-bottom: 42px;
    padding-top: 50px;
    margin-bottom: 26px;
    position: relative;
}

.mySwiper {
    max-width: 1600px;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    align-items: center;
    gap: 29px;
}

.slider__item {}

.slider__item {
    cursor: pointer;
}

.slider-pag {
    position: absolute;
    margin-left: 48%;
}