.product__item {
    max-width: 262px;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    margin-bottom: 35px;
    cursor: pointer;
    border-radius: 5px;
}

.product__item-img {
    background: #D8D8D8;
    width: 262px;
    height: 190px;
    object-position: center;
    object-fit: cover;
    border-radius: 5px;
    margin-bottom: 11px;
}

.product__item-title {
    font-family: 'Gilroy';
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
    color: #000000;
}

.product__item-time {
    font-family: 'Gilroy';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 14px;
    color: #4A4A4A;
    margin-bottom: 4px;
}

.product__item-price {
    font-family: 'Gilroy';
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    color: #000000;
}