.home__categories {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;

    &::before {
        content: "";
        position: absolute;
        display: inline-block;
        margin-left: -38px;
        width: 34px;
        top: 25px;
        height: 34px;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        background-image: url('./../../assets/images/arrow.svg');
        cursor: pointer;
    }

    &::after {
        content: "";
        position: absolute;
        display: inline-block;
        transform: rotate(180deg);
        top: 20px;
        right: -25px;
        cursor: pointer;
        width: 34px;
        height: 34px;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        background-image: url('./../../assets/images/arrow.svg');
    }
}


.home__categories-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.home__link {
    font-family: 'Gilroy';
    font-style: normal;
    font-weight: 900;
    font-size: 24px;
    line-height: 30px;
    color: #9B9B9B;
    text-decoration: none;
    position: relative;
    padding-bottom: 10px;
}

.home__links {
    display: flex;
    align-items: center;
    position: relative;
    gap: 77px;
    margin-top: 31px;
}

.nav-line {
    display: inline-block;
    width: 100%;
    height: 2px;
    background-color: #D5D5D5;
    margin-bottom: 22px;
    margin-top: 10px;
}



.watched-wrapper {
    display: flex;
    align-items: center;
    gap: 95px;
}

.home__link-input {
    display: none;
}

.liink::before {
    bottom: -12px;
}

.homeee::before {
    bottom: -1px;
}