.container {}

.auth__top {
    background: #FFFFFF;
    box-shadow: 0px 8px 14px 0px rgb(0 0 0 / 6%);
    padding-top: 16px;
    padding-bottom: 17px;
}

.auth {

    &__container {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__left {
        display: flex;
        flex-direction: column;
        max-width: 387px;
        width: 100%;
        justify-content: center;
        align-items: center;
    }

    &__left-heading {
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 700;
        font-size: 24px;
        line-height: 30px;
        text-align: center;
        text-transform: uppercase;
        color: #000000;
        margin-bottom: 22px;
    }

    &__left-success {
        display: none;
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 500;
        font-size: 17px;
        line-height: 23px;
        text-align: center;
        color: #304FFF;
        margin-bottom: 11px;
    }

    &__left-form {
        display: flex;
        flex-direction: column;
        max-width: 333px;
        width: 100%;
    }

    &__label-name {
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 500;
        font-size: 17px;
        line-height: 21px;
        color: #787993;
        margin-bottom: 24px;
        display: flex;
        flex-direction: column;
    }

    &__left-name-input {
        background: #FFFFFF;
        border: 1px solid rgba(151, 151, 151, 0.482592);
        border-radius: 5px;
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 500;
        font-size: 19px;
        line-height: 23px;
        margin-top: 7px;
        color: #C7C7C7;
        padding: 20px 0 19px 24.5px;
    }

    &__label-password {
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 500;
        font-size: 17px;
        line-height: 21px;
        color: #787993;
        margin-bottom: 31px;
        display: flex;
        flex-direction: column;
    }

    &__left-password-input {
        background: #FFFFFF;
        border: 1px solid rgba(151, 151, 151, 0.482592);
        border-radius: 5px;
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 500;
        font-size: 19px;
        line-height: 23px;
        margin-top: 7px;
        color: #C7C7C7;
        padding: 20px 0 19px 24.5px;
    }

    &__left-btn {
        background: #41D481;
        border-radius: 5px;
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 500;
        font-size: 17px;
        line-height: 21px;
        text-align: center;
        color: #FFFFFF;
        padding: 21px 140px 20px 141px;
        margin-bottom: 49px;
        border: none;
        cursor: pointer;
    }

    &__or-wrapper {
        display: flex;
        align-items: center;
        gap: 13px;
    }

    &__or-line {
        display: inline-block;
        height: 2px;
        width: 141.5px;
        margin-bottom: 15px;
        background-color: #979797;
    }

    &__or-text {
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 17px;
        text-align: center;
        color: #787993;
        margin-bottom: 15px;
    }

    &__or-recover {
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 700;
        font-size: 16px;
        line-height: 20px;
        text-align: center;
        color: #4E72CA;
        cursor: pointer;
    }

    &__right {
        height: 749px;
        width: 654px;
        background-image: url('./../../assets//images/auth-background.png');
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
    }

    &__right-wrapper {
        margin-top: 372px;
        margin-left: 61px;
        width: 275px;
        height: 275px;
        background: #FFFFFF;
        border-radius: 220px 220px 220px 25px;
        padding: 79px 20.31px 40.69px 41.46px;
    }

    &__right-logo {
        margin-bottom: 8.62px;
    }

    &__right-text {
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 700;
        font-size: 19px;
        line-height: 21px;
        color: #787993;
        margin-bottom: 18.92px;
    }

    &__right-btn {
        background: #FFFFFF;
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 700;
        font-size: 16px;
        line-height: 20px;
        color: #41D481;
        padding: 8.88px 17.69px 10.88px 18.54px;
        border: 2px solid #41D481;
        border-radius: 5px;
        cursor: pointer;
    }
}