.product-list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.more__button {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.product__line {
    display: inline-block;
    width: 100%;
    height: 2px;
    background-color: rgba(182, 202, 251, 0.606827);
    margin-bottom: 18px;
}

.more__button-text {
    font-family: 'Gilroy';
    font-size: 15px;
    line-height: 18px;
    color: #4E72CA;
    cursor: pointer;
    &::after {
        content: "";
        display: inline-block;
        width: 10px;
        height: 10px;
        margin-left: 9px;
        background-image: url("./../../assets/images/arrow-down.svg");
        background-repeat: no-repeat;
        background-size: contain;
        background-position: center;
    }
}

.moreee {
    margin-bottom: 30px;
    margin-top: 61px;
}