.header {

    &__container {
        background: #FFFFFF;
        box-shadow: 12px 4px 24px rgba(0, 0, 0, 0.0647645);
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    &__inner-wrapper {
        max-width: 1140px;
        display: flex;
        width: 100%;
        align-items: center;
    }

    &__logo-wrapper {
        display: flex;
        align-items: center;
        margin-right: 79px;
        text-decoration: none;
    }

    &__logo-img {
        width: 67px;
        height: 67px;
        object-fit: cover;
        margin-right: 14px;
    }

    &__logo-text {
        font-style: normal;
        font-weight: 700;
        font-size: 15px;
        line-height: 17px;
        color: #000000;
    }

    &__navbar {
        display: flex;
        align-items: center;
        max-width: 425px;
        width: 100%;
        justify-content: space-between;
        margin-right: 76px;
    }

    &__navbar-item {}

    &__link {
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 500;
        font-size: 15px;
        line-height: 18px;
        text-decoration: none;
        color: #000000;

        &:hover {
            color: #4E72CA;
        }
    }

    &__lang-wrapper {
        display: flex;
        align-items: center;
        gap: 15px;
        margin-right: 66px;
    }

    &__lang-item {
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 17px;
        color: #4A4A4A;
        text-decoration: none;
        cursor: pointer;

        &:hover {
            background-image: url('./../../assets/images/active-lang.svg');
            background-position: center;
            background-size: cover;
            background-repeat: no-repeat;
        }
    }

    &__lang-line {
        display: inline-block;
        height: 17px;
        width: 1px;
        background-color: #4A4A4A;
    }

    &__profile-wrapper {
        display: flex;
        align-items: center;
        gap: 15px;
    }

    &__profile-item {
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 700;
        font-size: 17px;
        line-height: 21px;
        color: #4E72CA;
        text-decoration: none;
    }

    &__prof-line {
        display: inline-block;
        height: 17px;
        width: 2px;
        background-color: #4E72CA;
    }

    &__profile {
        position: relative;
        text-decoration: none;
        font-family: 'Gilroy';
        font-style: normal;
        font-weight: 700;
        font-size: 15px;
        line-height: 19px;
        color: #19D476;
    }

    &__profile:hover {
        background-color: #19D476;
        border-radius: 10px 10px 0 0;
        color: #FFFFFF;
    }

    &__profile:hover .logOut {
        display: flex;
    }
}

.logOut {
    display: none;
    position: absolute;
    background-color: #19D476;
    border-radius: 0 0 10px 10px;
    width: 100%;
    top: 100%;
}

.logout__btn {
    border: none;
    background: #19D476;
    border-radius: 22px;
    color: #FFFFFF;
    font-family: 'Gilroy';
    font-style: normal;
    font-weight: 700;
    font-size: 15px;
    line-height: 19px;
    color: #FFFFFF;
    padding: 12px 28px;
    cursor: pointer;
}

.without {
    display: flex;
    align-items: center;
    gap: 18px;
}

.active-lang {
    background-image: url('./../../assets/images/active-lang.svg');
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}

.active3 {
    color: #4E72CA;
}
